@charset "UTF-8";
@import url(../styles/Common.scss);

.wrap.Funfilx{
    width: 100%;
    height: 100vh;

    .top_wrap.Funfilx{
        background: url(../../public/images/funfilx-Large.png)no-repeat 0 0;
        border-radius: 0px 0px 50px 52px;
        background-size: cover;
    }

    .info_top_wrap{
        color: #E73446;
    }

    .info_bottom_wrap{
        
         .info_left_side{
            h3{
                color: #E73446;
            }
         }

        .info_right_side{
            div{
                h3{
                    color: #E73446;
                }
                ul{  
                    margin: 0 0 20px 20px;}
               }
        }
    }

}

.mockup_desktop.Funfilx {
    width: 960px;
    background: url(../../public/images/funfilx-mockup1.png)no-repeat 0 0;
    background-size: cover;
    border-radius: 15px;
}
.mockup_tablet.Funfilx{
    width: 570px;
    height: 471px;
    background: url(../../public/images/funfilx-mockup2.png)no-repeat 0 0;
    background-size: cover;
    border-radius: 15px;
}
.mockup_phone.Funfilx{
    background: url(../../public/images/funfilx-mockup3.png)no-repeat 0 0;
    background-size: contain;
    border-radius: 15px;
}


